	module.exports = {"data":{"markdownRemark":{"html":"<p>僕は鏡音レン。僕と双子のリンと一緒にとあるマスターに買われたんだけど、そのマスターはすぐ病気で亡くなってしまって、遠縁のボーカロイド好きな親戚の家に行くことになった。葬式でその親戚が来るだろうから、挨拶しようと待っていたが、代理人としてミクがやって来た。遺族らは葬式にボーカロイドを代理で出すなんて失礼だ、とか嫌味も聞こえたが、マスターに買われてから余り日が経ってないせいか、さほど悲しい感情が浮かばない僕の方がよっぽど失礼だな、と思った。リンは別の親戚に引き取られることになったが、特に深い感情は湧かなかった。買われたときに初めて会ったわけだし、なのに双子って設定らしい。もう今となっては、顔もおぼろげ程度にしか覚えていない。 親戚の家は静かで薄暗かった。マスターは暫くの間旅に出ていて、ミクが留守番を任されているらしい。1か所、地下室への階段の先に鉄の扉があったが、厳重に鍵がかかっていて入れなかった。僕は静かなこの家を少しでも盛り上げてあげようと色々工夫し、ミクに提案したり、ミクを驚かせたりしてあげた。ミクはそれに精一杯応えてくれていた。あれほど静かで薄暗かった家は、またたく間に明るく賑やかな雰囲気に包まれるようになった。 いつしか、もうリンのことやマスターのことなどどうでも良くなっていた。ミクが僕のマスターのようにも思えて、それを自然と受け入れていた…… ……あの、鉄の扉を、開けてしまうまでは。 ほんの些細な好奇心だった。以前家に入ったドロボウをホーム・アローンさながらの神プレーで追い払った際に、落としていったピッキングツールで地下室の鉄扉を開けてしまったのだ。ドロボウを追い払った実績もあってオバケでもなんでも来い、と少し気が大きくなっていたのかもしれない。 中は肌寒く、そして時間に取り残されたようにそこだけ静かで薄暗かった。壁一面に古い書物やガラクタが整然と積み上げられていて、中央に長方形の大きな箱があった。箱の大きさは人1人分、その形はまるで棺のようだった。暗がりに目が慣れてくるにつれて、この箱が透明であることが解り、さらに扉を全開にして、地上階からの光を最大限に送り込むと、箱の中には人間がひとり、安らかな顔つきで眠っていた。その時は、不思議と怖いとかその類の感情は、浮かばなかった。 「あなたは、だれ……？」 僕は無意識に、静かに眠っているこの20後半～30半ば程の男に尋ねていた。しかし、眠っているためか、返事はない。念のため、もう一度尋ねてみる。 「あなたは、だれ……？」 「それは、私のマスターよ」 部屋に射す光が揺らぎ、後ろから聞こえる声。振り向くとそこにはミクがいた。 「久しぶりね、この部屋の暖炉に火を灯すのも」 それから暫くして、薄暗い地下室は暖炉の明りで温かく照らされていた。僕は最初ミクに地下室へ勝手に忍び込んだことを怒られるかと身構えたが、彼女の顔はいつも通りの笑顔で、僕に優しく抱きついてくれた。そして、僕の素朴な疑問に、色々答えてくれた。 「いつの間に、マスターは帰ってきていたんだ」 「……そうね」 「どうして、マスターは動かないの？」 「マスターは、時間が止まっているのよ」 「どうして、マスターの時間は止まっているの？」 「私が……止めたの」 「どうして？」 「私は、マスターといつまでも一緒にいたい。でも非情にも人間は、年を経ることで醜く朽ちて、やがてなくなってしまう。だから……私がマスターの時間を止めてあげたの」 「ねぇ、人の時間って止めることって出来るの？どうやって？」 「それは……ね」 さっきの質問の辺りからミクの返事の語尾が震えている。見上げるとミクの体も震えている。僕がミクに抱きつくと、ミクは僕の背中に両手を回してくれた。 「ミク姉……なんか怖いよ」 「大丈夫、怖がることなんて何もないのよ……大丈夫」 それから、永遠とも思える静寂が、僕を包みこんでいた。 「ミク姉、この二人はだぁれ？それに……この小さい方の人、昔どこかで見たことある」</p>","frontmatter":{"cover":null,"date":"2010-02-13T00:00:00+09:00","strDate":"2010/2/12","title":"本文"}}},"pathContext":{"path":"/ja/blog/2010/02/13-本文-8/","langKey":"ja"}}

